
onmessage = function(event) {
  const url = `http://localhost:3000/api/social/twdata/${event.unixTime}`;
  const req = new XMLHttpRequest();
  req.addEventListener('load', (data) => {
    postMessage(data);
  });
  req.open('GET', url);
  req.send();
};



